#pag-welcome {
  position: relative; /* Ensure the pseudo-element is positioned correctly */
  display: flex;
  text-align: center;
  align-items: center;
  justify-content: center;
  height: 100vh;
  background-color: black; /* Fallback color */
  background-image: url('/public/ptproiect.jpg'); 
  background-size: cover; 
  background-position: center;
  animation: none; /* Disable the initial animation */
}

#pag-welcome::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: black; /* Initial background */
  z-index: 0; /* Place it below other elements */
  animation: fadeOut 1.75s ease-in-out forwards;
}

@keyframes fadeOut {
  from {
    opacity: 1;
  }
  to {
    opacity: 0;
  }
}

#flexcon {
  position: relative;
  display: flex;
  flex-direction: column; 
  align-items: center; 
  z-index: 1; /* Ensure the content is above the pseudo-element */
}

@keyframes efectpr {
  from {
    opacity: 0;
  }
  to {
    opacity: 0.9;
  }
}

@keyframes efectpr1 {
  from {
    opacity: 0;
  }
  to {
    opacity: 0.8;
  }
}


.hover-text {
  display: none;
  position: absolute;
  bottom: -40px;
  left: 50%;
  transform: translateX(-50%);
  padding: 5px 10px;
  background-color: rgb(255, 102, 0);
  color: white;
  border-radius: 3px;
  white-space: nowrap;
  font-size: 18px;
}

.icon-container {
  position: relative;
  display: inline-block;
  margin-right: 35px;
}

.icon-container:hover .hover-text {
  display: block;
}
